body {
  color: #f3f3f3;
  background-color: #1a1a1a;
  font-family: Arial, sans-serif;
}

#logInpage, #container, #otherUserPage {
  width: 80%;
  background-color: #2e2e2e;
  border-radius: 10px;
  margin: 50px auto auto;
  padding: 20px;
}

li {
  cursor: pointer;
}

input[type="text"], input[type="password"], select {
  width: 100%;
  color: #f3f3f3;
  background-color: #3f3f3f;
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 10px;
}

.buttons-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.buttons-container button {
  margin-top: 20px;
}

button {
  color: #f3f3f3;
  cursor: pointer;
  background-color: #3f3f3f;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
}

button:hover {
  background-color: #4f4f4f;
}

h1, h2, h3 {
  text-align: center;
  border-bottom: 1px solid #3f3f3f;
  padding-bottom: 10px;
}

label, h2.username, img.profile-pic, p {
  text-align: center;
}

ul {
  flex-direction: column;
  align-items: center;
  display: flex;
}

li, li.user-item {
  cursor: pointer;
  text-align: center;
}

img.profile-pic {
  border-radius: 5px;
  margin: auto;
  display: block;
}

button.submit-status {
  margin: auto;
  display: block;
}

